import React from 'react';
import Dialog from './Dialog';

class Contact extends React.Component {
    constructor() {
        super();
        
        this.state = {
            textareaLength: 0,
            title: ' ',
            text: ' '
        }
    }
    onInput() {
        const value = document.querySelector('input').value;
        this.setState({
            title: value
        })
    }
    onTextarea() {
        const value = document.querySelector('textarea').value;
        const length = value.length;
        this.setState({
            textareaLength: length,
            text: value
        })
    }
    render() {
        let sendMail;
        if (this.props.popup) {
            const content = 
            <div>
                <input onChange={this.onInput.bind(this)} type="text" maxLength="40" placeholder=" "/>
                <textarea onChange={this.onTextarea.bind(this)} placeholder=" " rows="5"/>
                <sub>{this.state.textareaLength}</sub>
            </div>
            ; 
            // href="mailto:uiwwnw@icloud.com"
            sendMail = <Dialog dialogHeader="" dialogContent={content} fnSendMail={this.props.fnSendMail.bind(this)} title={this.state.title} text={this.state.text}/>
        };
        return (
            <article>
                <h2></h2>
                <button className="link"><i className="icon-mail"></i>uiwwnw@icloud.com</button>
                <a className="link" href="https://github.com/uiwwnw"><i className="icon-github-circled-alt"></i>https://github.com/uiwwnw</a>
                {sendMail}
            </article>
        );
    }
}

export default Contact;


