CA,iBACA,cACA,eAAgB,CA5HhC,2BA+HoB,cACA,WACA,YACA,WACA,iBAAkB,CAnItC,+BAuIoB,eAAgB,CAvIpC,aAuJQ,WACA,cACA,kBAEA,QACA,SACA,qBAGA,iBACA,+BACA,kBACA,WACA,gBACA,kBAAmB,CAxBnB,iBACI,GACI,UACA,uCAAyC,CAE7C,GACI,UACA,uCAAyC,CAAA,CAAA","file":"style.scss","sourcesContent":["// SCSS variables are information about icon's compiled state, stored under its original file name\n//\n// .icon-home {\n//   width: $icon-home-width;\n// }\n//\n// The large array-like variables contain all information about a single icon\n// $icon-home: x y offset_x offset_y width height total_width total_height image_path;\n//\n// At the bottom of this section, we provide information about the spritesheet itself\n// $spritesheet: width height image $spritesheet-sprites;\n$daum-name: 'daum';\n$daum-x: 0px;\n$daum-y: 149px;\n$daum-offset-x: 0px;\n$daum-offset-y: -149px;\n$daum-width: 149px;\n$daum-height: 148px;\n$daum-total-width: 446px;\n$daum-total-height: 297px;\n$daum-image: './spritesmith-generated/sprite.png';\n$daum: (0px, 149px, 0px, -149px, 149px, 148px, 446px, 297px, './spritesmith-generated/sprite.png', 'daum', );\n$facebook-name: 'facebook';\n$facebook-x: 298px;\n$facebook-y: 0px;\n$facebook-offset-x: -298px;\n$facebook-offset-y: 0px;\n$facebook-width: 148px;\n$facebook-height: 149px;\n$facebook-total-width: 446px;\n$facebook-total-height: 297px;\n$facebook-image: './spritesmith-generated/sprite.png';\n$facebook: (298px, 0px, -298px, 0px, 148px, 149px, 446px, 297px, './spritesmith-generated/sprite.png', 'facebook', );\n$kakao-name: 'kakao';\n$kakao-x: 149px;\n$kakao-y: 149px;\n$kakao-offset-x: -149px;\n$kakao-offset-y: -149px;\n$kakao-width: 149px;\n$kakao-height: 148px;\n$kakao-total-width: 446px;\n$kakao-total-height: 297px;\n$kakao-image: './spritesmith-generated/sprite.png';\n$kakao: (149px, 149px, -149px, -149px, 149px, 148px, 446px, 297px, './spritesmith-generated/sprite.png', 'kakao', );\n$line-name: 'line';\n$line-x: 298px;\n$line-y: 149px;\n$line-offset-x: -298px;\n$line-offset-y: -149px;\n$line-width: 148px;\n$line-height: 148px;\n$line-total-width: 446px;\n$line-total-height: 297px;\n$line-image: './spritesmith-generated/sprite.png';\n$line: (298px, 149px, -298px, -149px, 148px, 148px, 446px, 297px, './spritesmith-generated/sprite.png', 'line', );\n$mail-name: 'mail';\n$mail-x: 0px;\n$mail-y: 0px;\n$mail-offset-x: 0px;\n$mail-offset-y: 0px;\n$mail-width: 149px;\n$mail-height: 149px;\n$mail-total-width: 446px;\n$mail-total-height: 297px;\n$mail-image: './spritesmith-generated/sprite.png';\n$mail: (0px, 0px, 0px, 0px, 149px, 149px, 446px, 297px, './spritesmith-generated/sprite.png', 'mail', );\n$twitter-name: 'twitter';\n$twitter-x: 149px;\n$twitter-y: 0px;\n$twitter-offset-x: -149px;\n$twitter-offset-y: 0px;\n$twitter-width: 149px;\n$twitter-height: 149px;\n$twitter-total-width: 446px;\n$twitter-total-height: 297px;\n$twitter-image: './spritesmith-generated/sprite.png';\n$twitter: (149px, 0px, -149px, 0px, 149px, 149px, 446px, 297px, './spritesmith-generated/sprite.png', 'twitter', );\n$spritesheet-width: 446px;\n$spritesheet-height: 297px;\n$spritesheet-image: './spritesmith-generated/sprite.png';\n$spritesheet-sprites: ($daum, $facebook, $kakao, $line, $mail, $twitter, );\n$spritesheet: (446px, 297px, './spritesmith-generated/sprite.png', $spritesheet-sprites, );\n\n// The provided mixins are intended to be used with the array-like variables\n//\n// .icon-home {\n//   @include sprite-width($icon-home);\n// }\n//\n// .icon-email {\n//   @include sprite($icon-email);\n// }\n//\n// Example usage in HTML:\n//\n// `display: block` sprite:\n// <div class=\"icon-home\"></div>\n//\n// To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:\n//\n// // CSS\n// .icon {\n//   display: inline-block;\n// }\n//\n// // HTML\n// <i class=\"icon icon-home\"></i>\n@mixin sprite-width($sprite) {\n  width: nth($sprite, 5);\n}\n\n@mixin sprite-height($sprite) {\n  height: nth($sprite, 6);\n}\n\n@mixin sprite-position($sprite) {\n  $sprite-offset-x: nth($sprite, 3);\n  $sprite-offset-y: nth($sprite, 4);\n  background-position: $sprite-offset-x  $sprite-offset-y;\n}\n\n@mixin sprite-image($sprite) {\n  $sprite-image: nth($sprite, 9);\n  background-image: url(#{$sprite-image});\n}\n\n@mixin sprite($sprite) {\n  @include sprite-image($sprite);\n  @include sprite-position($sprite);\n  @include sprite-width($sprite);\n  @include sprite-height($sprite);\n}\n\n// The `sprites` mixin generates identical output to the CSS template\n//   but can be overridden inside of SCSS\n//\n// @include sprites($spritesheet-sprites);\n@mixin sprites($sprites) {\n  @each $sprite in $sprites {\n    $sprite-name: nth($sprite, 10);\n    .#{$sprite-name} {\n      @include sprite($sprite);\n    }\n  }\n}\n","@charset \"UTF-8\";\n@import \"./spritesmith-generated/sprite\";\n@include sprites($spritesheet-sprites);\n$headerHeight: 8rem;\n$footerHeight: 3rem;\n$fontSizeNormal: 1.2rem;\n$fontFamily: system-ui,\n-apple-system,\nBlinkMacSystemFont,\nsans-serif;\n$query-mobile: 'not (min-width: 640px)';\n$white: #fff;\n$gray: #ededed;\n$dark: #333;\n$black: #000;\n$bg: $black;\n$fwb: 700;\n$fwn: 400;\n$fwl: 200;\n$fwt: 100;\n$z-index: ( header: 201, dim: 200, popover: 500, popup: 1000, alert: 2000);\n@function z-index($key) {\n    @return map-get($z-index, $key);\n}\n\n%ellipsis {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n%vam {\n    white-space: nowrap;\n    font-size: 0;\n    >[data-align=\"middle\"] {\n        display: inline-block;\n        vertical-align: middle;\n        white-space: normal;\n        font-size: $fontSizeNormal;\n    }\n    &:before {\n        display: inline-block;\n        vertical-align: middle;\n        height: 100%;\n        min-height: inherit;\n        content: '';\n    }\n}\n\n%act {\n    position: relative;\n    &:after {\n        position: absolute;\n        top: 0;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        content: '';\n    }\n    &:active {\n        @keyframes active_after {\n            0% {\n                background: rgba(255, 255, 255, 0);\n            }\n            80% {\n                background: rgba(255, 255, 255, .3);\n            }\n            100% {\n                background: rgba(255, 255, 255, 0);\n            }\n        }\n        &:after {\n            animation: active_after .45s;\n        }\n    }\n}\n\n// tag setting \nbutton {\n    @extend %act;\n    padding: 0;\n    border: 0;\n    font-family: $fontFamily;\n    cursor: pointer;\n    background: none;\n}\n\ntextarea,\ninput {\n    margin: 0;\n    padding: 0;\n    border: 0;\n    font-family: $fontFamily;\n}\n\n// layout\nhtml {\n    font-size: 62.5%;\n}\n\nbody {\n    position: relative;\n    max-width: 102.4rem;\n    margin: auto;\n    padding: 0;\n    color: $white;\n    font-family: $fontFamily;\n    font-size: $fontSizeNormal;\n    background-color: $bg;\n    >div {\n        height: 100%;\n    }\n    * {\n        color: inherit;\n    }\n}\n\nmain {\n    .openPopup {\n        &:before {\n            position: absolute;\n            top: 0;\n            right: 0;\n            bottom: -10rem;\n            left: 0;\n            background: rgba(0, 0, 0, .637);\n            content: \"\";\n        }\n    }\n}\n.dim {\n    z-index: z-index(dim);\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: -10rem;\n    left: 0;\n    cursor: pointer;\n    background: rgba(0, 0, 0, .637);\n}\n// common\n[data-reactroot] {\n    .content {\n        min-height: 100vh;\n        padding: 0 1rem;\n        >article {\n            display: inline-block;\n            width: 100%;\n        }\n        &.hasHeader {\n            padding-top: $headerHeight;\n            min-height: calc(100vh - #{$headerHeight});\n        }\n        &.hasFooter {\n            min-height: calc(100vh - #{$footerHeight});\n            &.hasHeader {\n                min-height: calc(100vh - #{$headerHeight + $footerHeight});\n            }\n        }\n    }\n}\n\n.header {\n    z-index: z-index(header);\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    width: 100%;\n    max-width: 102.4rem;\n    height: $headerHeight;\n    margin: auto;\n    font-size: 2rem;\n    background: linear-gradient(to bottom, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.7) 70%, rgba(0, 0, 0, 0) 100%);\n    >* {\n        float: left;\n    }\n    h1 {\n        width: calc(100% - 4rem);\n        height: 100%;\n        line-height: $headerHeight;\n        margin: 0;\n        padding-right: 4rem;\n        box-sizing: border-box;\n        text-align: center;\n        font-size: 1.6rem;\n        font-weight: 400;\n        transition: opacity .4s;\n        .openPopup & {\n            opacity: 0.373;\n        }\n    }\n    button {\n        height: inherit;\n        font-size: inherit;\n        @at-root .callNav {\n            position: relative;\n\n            &:before {\n                z-index: 2;\n                position: fixed;\n                right: 0;\n                top: 0;\n                display: none;\n                width: 100%;\n                height: 8rem;\n                content: \"\";\n            }\n            i {\n                display: block;\n                width: 3rem;\n                margin: auto;\n                &:before {\n                    position: absolute;\n                    top: 50%;\n                    display: block;\n                    width: 3rem;\n                    height: .4rem;\n                    margin-top: -.8rem;\n                    border: .2rem solid #fff;\n                    border-right: 0;\n                    border-left: 0;\n                    transition: transform .3s, margin-top .3s, height .3s, border-width .3s;\n                    content: \"\";\n                }\n                &:after {\n                    position: absolute;\n                    top: 50%;\n                    display: block;\n                    width: 3rem;\n                    height: .2rem;\n                    margin-top: .4rem;\n                    transition: transform .6s, margin-top .6s;\n                    background: $white;\n                    content: \"\";\n                }\n            }\n            @media #{$query-mobile} {\n                &:hover {\n                    i {\n                        &:after {\n                            transform: rotate(-20deg);\n                        }\n                    }\n                }\n            }\n            &.active {\n                &:before {\n                    display: block;\n                }\n\n                i {\n                    &:before {\n                        height: 0;\n                        margin-top: -.4rem;\n                        border-top-width: .1rem;\n                        border-bottom-width: .1rem;\n                        transform: rotate(-45deg);\n                        transition: transform .6s, margin-top .6s, height 1s, border-color .3s;\n                    }\n                    &:after {\n                        margin-top: -.4rem;\n                        transform: rotate(-135deg);\n                        transition: transform 1s, margin-top .6s;\n                    }\n                }\n            }\n        }\n        &.callNav {\n            width: 4rem;\n        }\n    }\n    .menuBox {\n        width: calc(100% - 4rem);\n        height: 100%;\n        text-align: center;\n        button {\n            font-size: 1.6rem;\n        }\n    }\n}\n\n.nav {\n    @extend %vam; // z-index: z-index(popup);\n    z-index: z-index(popup);\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    text-align: center;\n    pointer-events: none;\n    animation: zoom .3s;\n    [data-align] {\n        width: 80%;\n    }\n    a {\n        display: block;\n        width: 100%;\n        line-height: 1.4;\n        font-size: 2rem;\n        text-align: center;\n        text-decoration: none;\n        poin