pse: collapse;\n\n                > * {\n                    display: table-cell;\n                    text-align: center;\n                    vertical-align: middle;\n                    border: 1px solid;\n                }\n            }\n        }\n    }\n    \n    input {\n        display: block;\n        width: 100%;\n        height: 2rem;\n        padding: 0 .5rem;\n        box-sizing: border-box;\n        border: .1rem solid #ddd;\n        border-radius: .3rem;\n    }\n\n    textarea {\n        display: block;\n        width: 100%;\n        margin-top: 1rem;\n        padding: 0 .5rem;\n        box-sizing: border-box;\n        border: .1rem solid #ddd;\n        border-radius: .3rem;\n        resize: none;\n    }\n\n    sub {\n        float: right;\n    }\n}\n\n// findmine\n#findMine {\n    &.restart {\n        &:after {\n            display: none;\n        }\n\n        .startPack {\n            display: none;\n        }\n    }\n    &.win ,\n    &.lose {\n        @keyframes lose {\n            0% {\n                opacity: 0;\n            }\n            100% {\n                opacity: 1;\n            }\n        }\n        &:after {\n            z-index: 1;\n            position: fixed;\n            top: 0;\n            right: 0;\n            bottom: 0;\n            left: 0;\n            background: rgba(0, 0, 0, .7);\n            animation: lose .8s;\n            content: \"\";\n        }\n    }\n    .startPack {\n        width: 40%;\n        margin: auto;\n        color: #000;\n        background: #fff;\n    \n        button {\n            display: block;\n            width: 100%;\n            font-size: 20px;\n        }\n    \n        input {\n            display: block;\n            width: 100%;\n            height: 30px;\n            margin-bottom: 10px;\n            text-align: center;\n            box-sizing: border-box;\n            font-size: 15px;\n        }\n        \n        sub {\n            display: block;\n            margin-top: -5px;\n     